import React, { useState, useEffect } from 'react';
import './Mypage.css';
import { useNavigate } from 'react-router-dom';
import useUserData from '../useUserData';
import mypageUser from '../images/mypageuser.png';
import anxietyImg from '../images/anxiety.png';
import fearImg from '../images/fear.png';
import sadnessImg from '../images/sadness.png';
import Select from 'react-select';
import axios from 'axios';

function Mypage() {
  const navigate = useNavigate();
  const {
    firstName,
    lastName,
    usernickname,
    birth,
    gender,
    userId,
    password,
    state,
    setFirstName,
    setLastName,
    setUsernickname,
    setBirth,
    setGender,
    setUserId,
    setPassword,
    setState,
    handleSave,
    fetchUserData,
  } = useUserData();
  const [profileImage, setProfileImage] = useState(mypageUser);

  useEffect(() => {
    // useUserData.js  fetchUserData    
    fetchUserData();
  }, []);

  useEffect(() => {
    //     
    setProfileImage(getProfileImage(state));
  }, [state]);

  const genderOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
  ];

  const stateOptions = [
    { value: '', label: '  ' },
    { value: '', label: '  ' },
    { value: '', label: '   ' },
  ];

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    switch (name) {
      case 'firstName':
        setFirstName(value);
        break;
      case 'lastName':
        setLastName(value);
        break;
      case 'usernickname':
        setUsernickname(value);
        break;
      case 'birth':
        setBirth(value);
        break;
      case 'password':
        setPassword(value);
        break;
      default:
        break;
    }
  };

  const handleGenderChange = (selectedOption) => {
    setGender(selectedOption ? selectedOption.value : '');
  };

  const handleStateChange = (selectedOption) => {
    const selectedState = selectedOption ? selectedOption.value : '';
    setState(selectedState);
    setProfileImage(getProfileImage(selectedState));
  };

  const getProfileImage = (state) => {
    switch (state) {
      case '':
        return sadnessImg;
      case '':
        return anxietyImg;
      case '':
        return fearImg;
      default:
        return mypageUser;
    }
  };

  const handleSaveWrapper = async (e) => {
    e.preventDefault();
    await handleSave();
    navigate('/');
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post(
        '/process/logout',
        {},
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      );

      if (response.status === 200) {
        alert(' .');
        navigate('/'); //     
      } else {
        throw new Error(' .  .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('   .');
    }
  };

  const customSelectStyles1 = {
    control: (provided) => ({
      ...provided,
      width: '420px',
      height: '25px',
      border: '2px solid #b6bec6',
      borderRadius: '10px',
      fontSize: '1rem',
      fontWeight: '400',
      color: '#787878',
      paddingLeft: '4.5%',
      paddingBottom: '12%',
    }),
    menu: (provided) => ({
      ...provided,
      width: '420px',
    }),
    indicatorSeparator: (provided) => ({
      ...provided,
      display: 'none',
    }),
  };

  const customSelectStyles2 = {
    control: (provided) => ({
      ...provided,
      width: '870px',
      height: '45px',
      border: '2px solid #b6bec6',
      borderRadius: '10px',
      fontSize: '1rem',
      fontWeight: '400',
      color: '#787878',
      paddingLeft: '2%',
    }),
    menu: (provided) => ({
      ...provided,
      width: '870px',
    }),
    indicatorSeparator: (provided) => ({
      ...provided,
      display: 'none',
    }),
  };

  return (
    <div className="myPage">
      <form onSubmit={handleSaveWrapper} className="userProfileForm">
        {/*    :  / */}
        <div className="userProfileContainer">
          <div className="userProfile">
            <img className="mypageImg" src={profileImage} alt=" "></img>
            <div className="userProfileBtn">
              <button type="button" className="imgUplode">
                 
              </button>
              <button type="button" className="imgDelete">
                 
              </button>
            </div>
          </div>
          {/*    */}
          <div className="userProfileInfo">
            <div className="userProfileName">
              <p>
                
                <input type="text" name="lastName" placeholder="" value={lastName} onChange={handleInputChange} />
              </p>
              <p>
                {' '}
                <input type="text" name="firstName" placeholder="" value={firstName} onChange={handleInputChange} />
              </p>
            </div>
            <div className="userProfileNickname">
              <p>
                
                <input
                  type="text"
                  name="usernickname"
                  placeholder=""
                  value={usernickname}
                  onChange={handleInputChange}
                />
              </p>
            </div>
          </div>
        </div>
        {/*    */}
        <div className="userProfileAdditionalInfo centered">
          <div className="additional1">
            <p className="a_id">
               <input type="text" placeholder="" value={userId} disabled />
            </p>
            {/*    */}
            <p className="a_pw">
              
              <input
                type="text"
                placeholder=""
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </p>
          </div>
          <div className="additional2">
            <p>
              
              <input
                type="text"
                name="birth"
                placeholder="(ex. 20240713)"
                value={birth}
                onChange={handleInputChange}
              />
            </p>
            
            <Select
              className="genderAlt"
              options={genderOptions}
              value={genderOptions.find((option) => option.value === gender)}
              onChange={handleGenderChange}
              placeholder=""
              styles={customSelectStyles1}
              isClearable
            />
          </div>
          <div className="additional3">
             {' '}
            <Select
              className="stateAlt"
              options={stateOptions}
              value={stateOptions.find((option) => option.value === state)}
              onChange={handleStateChange}
              placeholder="  ~."
              styles={customSelectStyles2}
              isClearable
            />
          </div>
        </div>
        {/*    */}
        <div className="centered">
          <button type="submit" className="saveBtn">
            
          </button>
          <button type="button" className="logoutBtn" onClick={handleLogout}>
            
          </button>
        </div>
      </form>
    </div>
  );
}

export default Mypage;
