import React, { useState, useEffect } from 'react';
import { Link, useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import CRUDHeader from './CRUDHeader';
import CreateComment from '../comments/CreateComment';
import community from '../../images/community.png';
import './CRUD.css';

function ReadNotice() {
  const { no } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    //     
    const fetchPostAndComments = async () => {
      try {
        const postResponse = await axios.get(`/notice/PostView/${no}`);
        setPost(postResponse.data.post);

        setLoading(false);
      } catch (error) {
        console.error('    :', error);
        setError('    ');
        setLoading(false);
      }
    };

    fetchPostAndComments();
  }, [no]);

  const handleDelete = async () => {
    const confirmDelete = window.confirm(' ?');

    if (confirmDelete) {
      try {
        await axios.delete(`/joy/Postview/${no}/process/delete`);
        alert(' .');
        navigate('/joy');
      } catch (error) {
        if (error.response && error.response.status === 403) {
          alert('  .');
        } else {
          console.error('    :', error);
          alert('    .');
        }
      }
    }
  };

  if (loading) {
    return <div> ...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  if (!post) {
    return <div>   .</div>;
  }

  const { title, nickname, created_date, content } = post;

  return (
    <div className="Read_all">
      <div>
        <div className="header_layout">
          <CRUDHeader title="" />
        </div>
        <div className="ReadTitle">{title}</div>
        <div className="infoUpdateDelete">
          <div className="info">
            <div></div>
            <div>{nickname}</div>
            <div>{created_date}</div>
          </div>
          <div className="updateDelete">
            <Link to={`/notice/Postview/${no}/process/update`}></Link>
            <div onClick={handleDelete} style={{ cursor: 'pointer' }}>
              
            </div>
          </div>
        </div>
        <div className="ReadContent">{content}</div>
      </div>
    </div>
  );
}

export default ReadNotice;
