import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import CRUDHeader from './CRUDHeader';
import './CRUD.css';
import axios from 'axios';

function UpdateJoy() {
  const { no } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState({
    title: '',
    body: '',
    file_data: null,
  });
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [canEdit, setCanEdit] = useState(false);

  useEffect(() => {
    axios
      .get(`/joy/Postview/${no}/process/update`)
      .then((response) => {
        const { title, content, file_data } = response.data;
        setPost({ title, body: content, file_data });
        setLoading(false);
        setCanEdit(true);
      })
      .catch((error) => {
        if (error.response && error.response.status === 403) {
          alert('  .');
          navigate(`/joy/PostView/${no}`);
        } else {
          console.error('    !', error);
          setError('    !');
          setLoading(false);
        }
      });
  }, [no, navigate]);

  const onChange = (event) => {
    const { value, name } = event.target;
    setPost({
      ...post,
      [name]: value,
    });
  };

  const onFileChange = (event) => {
    setFile(event.target.files[0]);
  };

  const updatePost = async (event) => {
    event.preventDefault();

    const formData = new FormData();
    formData.append('title', post.title);
    formData.append('content', post.body);
    if (file) {
      formData.append('file', file);
    }

    try {
      await axios.post(`/joy/Postview/${no}/process/update`, formData, {
        withCredentials: true,
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      alert('.');
      navigate(`/joy/PostView/${no}`);
    } catch (error) {
      if (error.response && error.response.status === 403) {
        alert('  .');
        navigate(`/joy/PostView/${no}`);
      } else {
        console.error('    :', error);
        alert('    :');
      }
    }
  };

  const backToList = () => {
    navigate(`/joy/PostView/${no}`);
  };

  if (loading) {
    return <div> ...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }
  if (!canEdit) {
    return <div>  .</div>;
  }

  return (
    <>
      <div>
        <CRUDHeader title=" " />
      </div>
      <form onSubmit={updatePost}>
        <div>
          <span></span>
          <input type="text" name="title" placeholder="" value={post.title} onChange={onChange} />
        </div>
        <br />
        <div>
          <span></span>
          <textarea name="body" placeholder="" value={post.body} onChange={onChange}></textarea>
        </div>
        <br />
        <div>
          <span>  </span>
          {post.file_data ? (
            <img src={`data:image/jpeg;base64,${post.file_data}`} alt="Current Upload" width="200" />
          ) : (
            <p>  .</p>
          )}
        </div>
        <div>
          <span>  </span>
          <input type="file" onChange={onFileChange} />
        </div>
        <br />
        <button type="button" onClick={backToList}>
          
        </button>
        <input type="submit" value=""></input>
      </form>
    </>
  );
}

export default UpdateJoy;
