import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import CRUDHeader from './CRUDHeader';
import './CRUD.css';
import axios from 'axios';

function UpdateNotice() {
  const { no } = useParams();
  const navigate = useNavigate();
  const [post, setPost] = useState({
    title: '',
    body: '',
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [canEdit, setCanEdit] = useState(false);

  useEffect(() => {
    axios
      .get(`/notice/Postview/${no}/process/update`)
      .then((response) => {
        const { title, content } = response.data;
        setPost({ title, body: content });
        setLoading(false);
        setCanEdit(true);
      })
      .catch((error) => {
        if (error.response && error.response.status === 403) {
          alert('  .');
          navigate(`/notice/PostView/${no}`);
        } else {
          console.error('    !', error);
          setError('    !');
          setLoading(false);
        }
      });
  }, [no, navigate]);

  const onChange = (event) => {
    const { value, name } = event.target;
    console.log(`name: ${name}, value: ${value}`);
    setPost({
      ...post,
      [name]: value,
    });
  };

  const updatePost = async (event) => {
    event.preventDefault();
    try {
      await axios.post(`/notice/Postview/${no}/process/update`, { title: post.title, content: post.body });
      alert('.');
      navigate(`/notice/PostView/${no}`);
    } catch (error) {
      if (error.response && error.response.status === 403) {
        alert('  .');
        navigate(`/notice/PostView/${no}`);
      } else {
        console.error('    :', error);
        alert('    :');
      }
    }
  };

  const backToList = () => {
    navigate(`/notice/PostView/${no}`);
  };

  if (loading) {
    return <div> ...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }
  if (!canEdit) {
    return <div>  .</div>; //      
  }

  return (
    <>
      <div>
        <CRUDHeader title=" " />
      </div>
      <form onSubmit={updatePost}>
        <div>
          <span></span>
          <input type="text" name="title" placeholder="" value={post.title} onChange={onChange} />
        </div>
        <br />
        <div>
          <span></span>
          <textarea name="body" placeholder="" value={post.body} onChange={onChange}></textarea>
        </div>
        <br />
        <button type="button" onClick={backToList}>
          
        </button>
        <input type="submit" value=""></input>
      </form>
    </>
  );
}

export default UpdateNotice;
