import React from 'react';
import './Signuppage.css';
import { useNavigate } from 'react-router-dom';
import Select from 'react-select';
import axios from 'axios';
import useUserData from '../useUserData';
import sadnessImg from '../images/sadness.png';
import anxietyImg from '../images/anxiety.png';
import fearImg from '../images/fear.png';
import userImg from '../images/mypageuser.png';

function Signuppage() {
  const navigate = useNavigate();
  const {
    firstName,
    lastName,
    gender,
    birth,
    usernickname,
    userId,
    password,
    state,
    setFirstName,
    setLastName,
    setGender,
    setBirth,
    setUsernickname,
    setUserId,
    setPassword,
    setState,
    handleSave,
  } = useUserData();

  const handleGenderChange = (selectedOption) => {
    setGender(selectedOption ? selectedOption.value : '');
  };

  const handleStateChange = (selectedOption) => {
    setState(selectedOption ? selectedOption.value : '');
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (lastName && firstName && usernickname && birth && gender && userId && password && state) {
      const profileImage = getProfileImage(state);
      const signupData = {
        lastName,
        firstName,
        usernickname,
        birth,
        gender,
        username: userId,
        password,
        state,
        profileImage,
      };

      try {
        //      
        const response = await axios.post('/loginpage/process/signup', signupData);
        const result = response.data;

        if (result.success) {
          alert(' !');
          navigate('/loginpage');
        } else {
          alert(result.message);
        }
      } catch (error) {
        console.error('    :', error);
        alert('    .');
      }
    } else {
      alert('  .');
    }
  };

  const getProfileImage = (state) => {
    switch (state) {
      case '':
        return sadnessImg;
      case '':
        return anxietyImg;
      case '':
        return fearImg;
      default:
        return userImg;
    }
  };

  const genderOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
  ];

  const stateOptions = [
    { value: '', label: '  ' },
    { value: '', label: '  ' },
    { value: '', label: '   ' },
  ];

  const customSelectStyles = {
    control: (provided) => ({
      ...provided,
      width: '492px',
      height: '48px',
      marginBottom: '15px',
      border: '1px solid #d9d9d9',
      borderRadius: '4px',
      fontSize: '15px',
      fontWeight: '700',
      color: '#787878',
      paddingLeft: '3%',
      paddingBottom: '55px',
    }),
    menu: (provided) => ({
      ...provided,
      width: '464px',
    }),
    indicatorSeparator: (provided) => ({
      ...provided,
      display: 'none',
    }),
  };

  return (
    <div className="SignupPage">
      <h1></h1>
      <form onSubmit={handleSubmit}>
        <div className="inputUsername">
          <div className="firstnameInput">
            <input
              type="text"
              placeholder=""
              value={firstName}
              onChange={(e) => setFirstName(e.target.value)}
              required
            />
          </div>
          <div className="lastnameInput">
            <input
              type="text"
              placeholder=""
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
              required
            />
          </div>
        </div>
        <div className="inputUserInfo">
          <input
            type="text"
            placeholder=""
            value={usernickname}
            onChange={(e) => setUsernickname(e.target.value)}
            required
          />
          <input
            type="text"
            placeholder="(ex. 20240713)"
            value={birth}
            onChange={(e) => setBirth(e.target.value)}
            required
          />
          <Select
            className="genderLabel"
            options={genderOptions}
            onChange={handleGenderChange}
            placeholder=""
            styles={customSelectStyles}
            isClearable
            required
          />
          <input type="text" placeholder="" value={userId} onChange={(e) => setUserId(e.target.value)} required />
          <input
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <p>  ?</p>
          <Select
            className="stateLabel"
            options={stateOptions}
            onChange={handleStateChange}
            placeholder="  ~."
            styles={customSelectStyles}
            isClearable
            required
          />
        </div>
        <button type="submit" className="SignupBtn">
          
        </button>
      </form>
    </div>
  );
}

export default Signuppage;
