import React from "react";
import { Link } from "react-router-dom";
import "./Hospital.css";
import arrowImg from "../images/arrow.png";

function Hospital() {
  return (
    <div className="hospitalmainPage">
      <div className="hospitalHeader">
        <h1>
             !
          <br />
             !
        </h1>
      </div>
      <div className="doctorTap">
        <h2>A /  </h2>
        <p>  </p>
        <Link to="/hospital/HospitalA">
          <img className="arrowIcon" src={arrowImg} alt="A " />
        </Link>
      </div>
      <div className="doctorTap">
        <h2>B /  </h2>
        <p> </p>
        <Link to="/hospital/HospitalB">
          <img className="arrowIcon" src={arrowImg} alt="B " />
        </Link>
      </div>
      <div className="doctorTap">
        <h2>C /  </h2>
        <p></p>
        <Link to="/hospital/HospitalC">
          <img className="arrowIcon" src={arrowImg} alt="C " />
        </Link>
      </div>
    </div>
  );
}

export default Hospital;
