import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./Loginpage.css";
import axios from "axios";

function Loginpage() {
  const navigate = useNavigate();
  const [id, setId] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const response = await axios.post("/loginpage/process/login", {
        id,
        password,
      });
      const data = response.data;

      if (data.success) {
        alert(" !");
        navigate("/"); //     
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  const handleKakaoLogin = () => {
    const REST_API_KEY = "f4dfb2e0ef054a0117ea1b187b8a8eb5"; //   
    const REDIRECT_URI = "http://localhost:3000";
    const kakaoAuthUrl = `https://kauth.kakao.com/oauth/authorize?client_id=${REST_API_KEY}&redirect_uri=${REDIRECT_URI}&response_type=code`;
    window.location.href = kakaoAuthUrl;
  };

  return (
    <div className="loginPage">
      <h1></h1>
      <form className="loginPagecontent" onSubmit={handleSubmit}>
        <div className="textBox">
          <input
            type="text"
            name="id"
            placeholder=""
            value={id}
            onChange={(e) => setId(e.target.value)}
          />
          <input
            type="password"
            name="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <div className="btnContent">
          <button type="submit" className="loginpageBtn">
            
          </button>
          <button className="signuppageBtn">
            <Link to="/Signuppage" className="signupLink">
              
            </Link>
          </button>
          <button type="button" className="kakaoBtn" onClick={handleKakaoLogin}>
              
          </button>
        </div>
      </form>
    </div>
  );
}

export default Loginpage;
