import React, { useEffect } from 'react';

const { kakao } = window;

function KakaoMapA() {
  useEffect(() => {
    const markerPosition = new kakao.maps.LatLng(37.66819314126892, 127.11618926405504);

    const container = document.getElementById('map'); //   div
    const options = {
      center: markerPosition, //  
      level: 11, //   
    };

    //   div      
    const map = new kakao.maps.Map(container, options);

    //  
    const marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    //     
    marker.setMap(map);

    const iwContent =
      '<div style="width:151px;height:24px;display:flex;justify-content:center;padding-top:4px"> A </div>'; //    HTML  document element 

    //  
    const infowindow = new kakao.maps.InfoWindow({
      position: markerPosition,
      content: iwContent,
    });

    //    .   marker     
    infowindow.open(map, marker);
  }, []);

  return <div id="map" style={{ width: '70%', height: '400px' }}></div>;
}

export default KakaoMapA;
