import React from 'react';
import Community from './Community';
import './AllCommunity.css';
import community from '../../images/community.png';

function AllCommunity() {
  return (
    <div className="all">
      <div className="all_Community_layout">
        <div className="title_layout">
          <img src={community} alt=" " width={27} height={26} />
          <div className="community_name"></div>
        </div>
        <div>
          <div className="Community_layout">
            <Community title=" " emotion="joy"></Community>
            <Community title=" " emotion="sadness"></Community>
          </div>
          <div className="Community_layout">
            <Community title=" " emotion="anxiety"></Community>
            <Community title=" " emotion="fear"></Community>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AllCommunity;
