import React from 'react';
import './ElementCommunity.css';

function TitleBodyCommunity(props) {
  return (
    <>
      <h1>{props.title} </h1>
      <div>
        <div className="TitleBodyCommunity_body"> {props.title} .</div>
        <div>{props.body}</div>
      </div>
    </>
  );
}

export default TitleBodyCommunity;
